.App {
  /* text-align: center; */
  /* display: flex;
  flex-direction: column; */
}

.container {
  text-align: center;
  align-items: center;
  align-content: center;
  justify-content: center;
  margin-bottom: 5%;
}

.excel-button {
  border: none;
  /* Remove border */
  background-color: transparent;
  /* Make background transparent */
  cursor: pointer;
  /* Change cursor to pointer on hover */
  padding: 0;
  /* Remove padding to adjust size */
  font-size: inherit;
  /* Inherit font size */
  color: inherit;
  /* Inherit text color */
}

.body {
  margin-top: 30px;
}

.container_header {
  margin: 0;
  background-color: black;
  color: white;
}

.submit_button {
  display: flex;
  margin-left: 10%;
}

.submit_button2 {
  background-color: #2296F3;
  border: 0px solid #2296F3;
}

.add_stop_button:active:first-of-type {
  /* margin-top: 10px; */
  display: flex;
  /* width: 80%; */
  justify-content: space-evenly;
}

.add_stop_button2 {
  flex-grow: 1;
  flex-shrink: 1;
  /* margin-right: 76px; */
}

.add_stop_button2:active:first-of-type {
  /* margin-right: 76px; */
}

.remove_last_stop_button {
  flex-grow: 1;
  flex-shrink: 1;
}

.header_results {
  margin: 0px;
  background-color: black;
  color: white;
  display: flex;
  justify-content: center;
  flex-direction: row-reverse;
  align-items: center;
}

.header_results button {
  position: relative;
  right: 20%;
}

.downward-arrow {
  position: relative;
  right: 32%;
  top: 32px;
}

.card {
  border: 1px solid #A1A1A1;
  border-radius: 15px;
  width: 90%;
}

.card_inside {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 90%;
}

.card_header {
  font-size: 20px;
}

.card_p {
  position: relative;
  right: 5%;
  font-size: 15px;
}

.col3_card_inside {
  width: 80%;
  position: relative;
  right: 9%;
}

.card_p1 {
  font-size: 15px;
  border: 1px solid #EFEFEF;
  border-radius: 15px;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 15px;
  padding-right: 15px;
  background-color: #EFEFEF;
  /* position: relative;
  right: 5%; */
  direction: rtl;
}

.card_p2 {
  font-size: 15px;
  border: 1px solid #EFEFEF;
  border-radius: 15px;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 15px;
  padding-right: 15px;
  background-color: #EFEFEF;
  position: relative;
  right: 7%;
  direction: rtl;
}

.cards {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

input {
  border: 1px solid #2296F3;
  border-radius: 15px;
  font-size: 10px;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-right: 10px;
  width: 70%;
  direction: rtl;
}

.input2 {
  margin-top: 10px;
}

.button_input2 {
  position: relative;
  left: 92px;
  bottom: 35px;
}

.time_dropdown {
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
}

.col2_date_dropdown_input2 {
  display: flex;
  align-items: stretch;
  justify-content: center;
  margin-top: 10px;
}

.date_dropdown {
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
}

button {
  border-radius: 15px;
  background-color: black;
  color: white;
  font-size: 13px;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-right: 15px;
  padding-left: 15px;
  font-family: 'Poppins', 'SemiBold';
}

label {
  display: flex;
  flex-direction: column;
  font-size: 13px;
  width: 80%;
  text-align: right;
  padding-right: 10%;
  margin-top: 15px;
}

hr {
  margin-top: 5%;
  width: 80%;
}

.fixed-button {
  flex: 1;
}

.start_address {
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
}

/* .stop_address {
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
} */

.end_address {
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
}

.start_address div {
  width: 100%;
  display: flex;
  align-items: stretch;
  justify-content: center;
}

